.header_container {
  border-radius: 10px;
  padding-top: 14px;
  padding-bottom: 24px;
  max-width: 1280px;
  background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.4) 65.37%
    ),
    url(../img/orange_bcg_mobile.jpeg);
  background-position: top right;
  background-repeat: no-repeat;
  background-size: cover;
  orientation: portrait;

  @media screen and (min-width: 768px) {
    padding: 16px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }

  @media screen and (min-width: 1280px) {
    padding: 16px 32px 32px 32px;
  }
}

@media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header_container {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.4) 65.37%
      ),
      url(../img/orange_bcg_mobile_2x.jpeg);
  }
}

@media screen and (min-width: 768px) {
  .header_container {
    background-position: top right;
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.4) 65.37%
      ),
      url(../img/orange_bcg_desktop.jpeg);
  }
}

@media screen and (min-width: 768px) and (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header_container {
    orientation: landscape;
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.4) 65.37%
      ),
      url(../img/orange_bcg_desktop_2x.jpeg);
  }
}

.btn_group {
  display: flex;
  gap: 4px;

  @media screen and (min-width: 768px) {
    align-items: start;
    margin-bottom: 105px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 151px;
  }
}

.btn_lib {
  display: flex;
  justify-content: center;
}

.logo_lib {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  text-align: center;
  padding-top: 300px;
  padding-bottom: 16px;
  font-weight: 500;
  font-size: 32px;
  line-height: calc(38 / 32);
  transition: all 0.3s ease-in-out;

  @media screen and (min-width: 768px) {
    justify-content: start;
    padding: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: calc(47 / 40);
  }

  @media screen and (min-width: 1280px) {
    font-size: 64px;
    line-height: calc(75 / 64);
  }
}

.logo_lib:hover {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5), 0 0 0 #b92f2c,
    1px 1px 5px #b92f2c;
}

.logo_lib::before {
  margin-right: 8px;
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../img/film-svg.svg);
  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }
  @media screen and (min-width: 1280px) {
    width: 59px;
    height: 59px;
  }
}
.logo_lib:hover::before {
  opacity: 0.8;
  animation: logo-rotate 2s linear infinite;
}

@keyframes logo-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
